}

body {
  margin: 0;
  font-family: var(--font-base);
  background-color: var(--color-base-bg-1);
  color: var(--color-base-text-1);
  font-size: 16px;
  transition: background-color 0.3s;
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

.page {
  .wrapper {
    flex: 1;
  }
}
/* Estilo del botón de disponibilidad */
.status-btn {
  display: inline-flex;
  align-items: center;
  padding: 10px 20px;
  background-color: #000;
  color: white;
  border-radius: 10px;
  font-size: 16px;
  font-weight: 500;
  text-decoration: none;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.15);
  transition: background-color 0.3s ease, border 0.3s ease;
  border: 2px solid transparent;
  margin-top: 30px;
}

/* Punto verde */
.status-dot {
  width: 10px;
  height: 10px;
  background-color: #00e676; /* verde brillante */
  border-radius: 50%;
  margin-right: 10px;
}

/* Hover */
.status-btn:hover {
  background-color: #111;
  opacity: 0.9;
}

/* Estilo para cuando el modo oscuro está activado (detectado por atributo) */
html[data-mode="dark"] .status-btn {
  border: 2px solid #666;
}

html[data-mode="dark"] .status-btn:hover {
  border-color: #666;
}








