// Estilos para el menú principal (hover en todas las páginas)
.menu-main ul li > a {
  transition: all 0.3s ease;
  position: relative;

  &:hover {
    color: #444; // Cambio de color
    transform: scale(1.05); // Efecto de zoom
    text-shadow: 0 0 8px rgba(0, 0, 0, 0.01); // Sombra/glow

    // Opcional: Subrayado animado
    &::after {
      content: "";
      position: absolute;
      width: 83%;
      height: 2px;
      bottom: 6px;
      left: 0;
      /*transform-origin: left;*/
      background-color: #cccccc;
      animation: underline 0.3s ease forwards;
    }
  }
}

@keyframes underline {
  from { transform: scaleX(0); }
  to { transform: scaleX(1); }
}





// Modo oscuro (se activa con data-mode="dark")
[data-mode="dark"] .menu-main ul li > a {
  color: white; // Color claro para fondo oscuro

  &:hover {
    color: white; // Verde agua (o elijas)
    text-shadow: 0 0 8px rgba(255, 255, 255, 1);
  }

  &::after {
    background-color: #cccccc; // Mismo color que el hover
  }
}





.logo-text a {
  position: relative;
  text-decoration: none;
  color: inherit;
  transition: all 0.3s ease;
  display: inline-block;

  &:hover {
    color: #444; // Color más oscuro al hacer hover (modo claro)
    transform: scale(1.05); // Zoom suave
    text-shadow: 0 0 8px rgba(0, 0, 0, 0.1); // Glow sutil

    &::after {
      content: '';
      position: absolute;
      width: 100%;
      height: 2px;
      bottom: 0px;
      left: 0;
      background-color: #cccccc;
      animation: underline 0.3s ease forwards;
    }
  }

  &::after {
    content: '';
    position: absolute;
    width: 0%;
    height: 2px;
    bottom: 6px;
    left: 0;
    background-color: transparent;
    transition: width 0.3s ease-in-out;
  }
}

// Modo oscuro
html[data-mode='dark'] .logo-text a {
  color: var(--color-logo-text) !important;

  &:hover {
    color: var(--color-logo-text) !important;
