.menu-main-mobile::before {
  content: "";
  position: absolute;
  width: 200%;
  height: 200%;
  left: -50%;
  top: -50%;
  background: radial-gradient(circle at 50% 50%, rgba(255,255,255,1) 40%, transparent 50%);
  animation: pulse 3s ease-in-out infinite;
  z-index: 0;
  pointer-events: none;
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
    opacity: 0.2;
  }
  50% {
    transform: scale(1.3);
    opacity: 0.1;
  }
}








