  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.05);
  font-family: 'Inter', sans-serif;
}

form label {
  display: block;
  margin-bottom: 2px; /* antes 4px */
  font-weight: 400;
  color: #333;
}

form input,
form textarea {
  width: 100%;
  padding: 8px 12px; /* antes 10px 14px */
  margin-bottom: 10px; /* antes 14px */
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 15px;
  font-family: 'Inter', sans-serif;
  resize: vertical;
}

form textarea {
  min-height: 90px; /* antes 100px */
}

form button[type="submit"] {
  background-color: #000;
  color: #fff;
  border: none;
  padding: 10px 20px; /* antes 12px 24px */
  border-radius: 6px;
  font-size: 15px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-top: 5px;
}

form button[type="submit"]:hover {
  background-color: #333;
}

@media (max-width: 600px) {
  form {
    padding: 10px; /* antes 15px */
    width: 90%;
  }

  form input,
  form textarea {
    font-size: 14px;
    padding: 8px 10px;
    margin-bottom: 8px;
  }

  form button[type="submit"] {
    width: 100%;
  }
}











