.logos {
  margin-right: auto;
  a {
    display: block;
    opacity: 1;
    &:hover {
      opacity: 0.7;
    }
    img {
      vertical-align: bottom;
    }
  }
  .logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    .logo-image {
      margin-right: 4px;
    }
    .logo-text {
      font-size: 18px !important;
      font-family: 'Inter', sans-serif !important;
      font-weight: 400 !important;
      color: var(--color-logo-text);
      a {
        color: var(--color-logo-text);
      }
    }
  }
  .logo-desktop {
    display: none;
    @include media-breakpoint-up(sm) {
      display: flex;
    }
  }
  .logo-mobile {
    display: flex;
    @include media-breakpoint-up(sm) {
      display: none;
    }
  }
  .logo-desktop-invert {
    display: none;
  }
  .logo-mobile-invert {
    display: none;
  }
}
html[data-mode="dark"] {
  .logo-desktop {
    display: none;
  }
  .logo-mobile {
    display: none;
  }
  .logo-desktop-invert {
    display: none;
    @include media-breakpoint-up(sm) {
      display: flex;
    }
  }
  .logo-mobile-invert {
    display: flex;
    @include media-breakpoint-up(sm) {
      display: none;
    }
  }
}
